@import '../common/styles/sass/mixins';

.footer {
  @include flex(row, center, stretch, nowrap);

  position: relative;
  min-height: 300px;
  background-color: var(--background-primary);

  .container {
    @include container;
    @include flex(column, flex-start, center, nowrap);

    padding: 100px 0 20px;

    .socialIcons {
      @include flex(row, space-between, center, nowrap);

      width: 400px;
      margin-top: 30px;

      .socialIcon {
        width: 60px;
        height: 60px;

        opacity: 0.5;
        border: 1px solid var(--text-color);
        border-radius: 50%;

        a {
          @include flex(row, center, center, nowrap);

          width: 100%;
          height: 100%;

          .telegramIcon,
          .linkedinIcon,
          .codewarsIcon,
          .githubIcon,
          .mailIcon {
            width: 60%;
          }
        }

        &:hover {
          opacity: 1;
          transition: opacity 0.2s ease;
        }

        @media screen and (width <= 380px) {
          width: 50px;
          height: 50px;
        }
      }

      @media screen and (width <= 505px) {
        width: 100%;
      }
    }

    .copyright {
      @include text(var(--text-font-family), 14px, 1.8, 300);

      margin-top: 40px;
      opacity: 0.6;
    }
  }
}
