.app {
  position: relative;

  .scrollButton {
    all: unset;

    cursor: pointer;

    position: fixed;
    z-index: 1000;
    right: 10px;
    bottom: 10px;

    box-sizing: border-box;

    .upArrowIcon {
      width: 35px;
      height: 35px;
      padding: 3px;

      opacity: 0.3;
      border: 1px solid var(--text-color);
      border-radius: 50%;

      & path {
        fill: var(--text-color);
      }

      &:hover {
        opacity: 1;
        transition: opacity 0.2s ease;
      }
    }
  }
}
